body {
    font-family: 'Arial', sans-serif;
    background-color: #151526;
    color: gray;;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
    padding: 20px;
}

.container {
    background: #282c3f;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
    max-width: 100vw;
    width: 100%;
}

h1 {
    font-size: 20px;
    margin-bottom: 10px;
}

.price {
    font-size: 22px;
    font-weight: bold;
    color: #ffcc00;
}

.crypto-select {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 15px;
}

.input {
    padding: 8px;
    width: 90%;
    font-size: 16px;
    border: 2px solid #aaa;
    border-radius: 5px;
    outline: none;
    background: #fff;
    color: #000;

}

.button {
    padding: 8px 12px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
}

.button:hover {
    background: #0056b3;
}

.purchase-section {
    background: #383d52;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.2);
    margin-bottom: 15px;
}

.purchase-section label {
    display: block;
    font-size: 14px;
    margin-top: 8px;
    text-align: left;
}

.quantity {
    font-size: 16px;
    font-weight: bold;
    color: #00d4ff;
}

.result {
    background: #383d52;
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.2);
    margin-top: 15px;
}

.profit-loss {
    font-size: 18px;
    font-weight: bold;
    padding: 8px;
    border-radius: 5px;
    display: inline-block;
    width: 90%;
}

.profit {
    color: #00ff00;
    background: rgba(0, 255, 0, 0.2);
}

.loss {
    color: #ff4444;
    background: rgba(255, 0, 0, 0.2);
}

/* üìå –ê–î–ê–ü–¢–ò–í–ù–û–°–¢–¨ */
@media (max-width: 600px) {
    .container {
        max-width: 90%;
        padding: 15px;
    }

    h1 {
        font-size: 18px;
    }

    .price {
        font-size: 20px;
    }

    .input {
        font-size: 14px;
        padding: 6px;
    }

    .button {
        font-size: 12px;
        padding: 6px 10px;
    }

    .profit-loss {
        font-size: 16px;
    }
}

.color-gray{
    color: gray;
}